* {
    box-sizing: border-box;
  }
  
  html {
    font-family: 'Open Sans', sans-serif;
    font-size: 18px;
  }
  
  body {
    background: #f3efe8 url('/img/bg.svg') center/cover; 
    width: 100vw;
    height: 100vh;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  @keyframes myanim {
    0% { background-color: pink;}
    50% { background-color: red;}
    100% { background-color: pink;}
  }
  .calc {
    width: 20rem;
    background-color: white;
    box-shadow: 0px 0px 32px gray;
    transition: 5s;
    animation-name: myanim;
    animation-duration: 5s;
    animation-iteration-count: 10;
  
    &__result {
      height: 3.5rem;
      line-height: 3.5rem;
      color: white;
      text-align: right;
      background-color: #867666;
      border: none;
      outline: none;
      font-size: 220%;
      padding: 0 1rem;
      font-weight: 300;
    }
    
    &__input {
      width: 100%;
      display: block;
      height: 2.5rem;
      line-height: 2.1rem;
      color: white;
      text-align: right;
      background-color: #e1b80D;
      border: none;
      outline: none;
      font-size: 150%;
      padding: 0.4rem 1rem 0 1rem;
      font-weight: 300;
    }
    
    &__btn {
      width: 3rem;
      height: 3rem;
      background-color: #eee;
      cursor: pointer;
      border: none;
      font-size: 150%;
      font-weight: 300;
    }
    
    &__btn--digit {
      width: 3rem;
      height: 3rem;
      background-color: white;
      cursor: pointer;
      border: none;
      font-size: 150%;
      font-weight: 300;
    }
    
    &__btn:hover {
      background-color: #ddd;
    }
    
    &__btn--digit:hover {
      background-color: #eee;
    }
    
    &__controls {
      display: flex;
      flex-wrap: wrap;
    }
  }
  
